
@charset "UTF-8";
.img-src {
  font: 0/0 a;
}
.img-src > img {
    max-width: 100%;
    max-height: 100%;
}
.img-top,
.img-middle,
.img-bottom {
  font-size: 0;
  text-align: center;
}
.img-top:after,
.img-middle:after,
.img-bottom:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 100%;
}
.img-top:after,
.img-top > img {
  vertical-align: top;
}
.img-middle:after,
.img-middle > img {
  vertical-align: middle;
}
.img-bottom:after,
.img-bottom > img {
  vertical-align: bottom;
}
body {
  margin: 0;
  padding: 0;
  background: #fff;
  font-size: 14px;
  color: #333;
  font-family: Helvetica, Arial, 'PingFangSC', 'Hiragino Sans GB', 'Microsoft YaHei', sans-serif;
}
.container {
  max-width: 1170px;
  padding-left: 15px;
  margin-left: auto;
  margin-right: auto;
}
.container:after {
    content: '';
    display: table;
    clear: both;
}
@media (min-width: 768px) {
.container {
      width: 750px;
}
}
@media (min-width: 992px) {
.container {
      width: 970px;
}
}
@media (min-width: 1025px) {
.container {
      width: auto;
      padding-left: 0;
}
}
@media (max-width: 1200px) {
.container {
      width: 1170px;
}
}
.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
}
#header {
  position: relative;
  z-index: 1;
  height: 60px;
}
#header .content {
    height: 100%;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
#header .content h1 {
      margin: 0 0 0 50px;
      font-size: 24px;
      line-height: 60px;
}
.banner {
  height: 500px;
}
.banner .banner-image {
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}
.banner .hidden-image {
    width: 0px;
    height: 0px;
    opacity: 0;
}
/* 列表 */
.card-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  max-width: 85%;
  margin: 30px auto;
}
.card-list .item {
    margin-top: 20px;
    min-width: 25%;
    max-width: 25%;
    padding: 0 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.card-list .content {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    width: 100%;
    height: 100%;
    background: #fff;
    overflow: hidden;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.card-list .content:hover {
      -webkit-transition: box-shadow .25s;
      -webkit-transition: -webkit-box-shadow .25s;
      transition: -webkit-box-shadow .25s;
      transition: box-shadow .25s;
      transition: box-shadow .25s, -webkit-box-shadow .25s;
      -webkit-box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
              box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card-list .content:hover img {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
}
.card-list .embed-responsive {
    padding-bottom: 140%;
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
}
.card-list .cover {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
}
.card-list .cover img {
      width: 100%;
      height: 100%;
      -webkit-transition: -webkit-transform 2s ease;
      transition: -webkit-transform 2s ease;
      transition: transform 2s ease;
      transition: transform 2s ease, -webkit-transform 2s ease;
}
.card-list .info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 20px;
}
.card-list .info .title {
      margin: 0;
      margin-bottom: 15px;
      font-size: 20px;
      line-height: 1.4;
      font-weight: normal;
      cursor: pointer;
}
.card-list .info .title a {
        position: relative;
        display: block;
        text-decoration: none;
        color: #333;
        padding-right: 20px;
}
.card-list .info .title i {
        position: absolute;
        top: 2px;
        right: 0;
        width: 20px;
        height: 20px;
        background: url(data:image/png;base64,);
        background-size: 20px;
}
.card-list .info .stat {
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.card-list .info .stat .checked {
        width: 20px;
        height: 20px;
        background: url(data:image/png;base64,);
        background-size: 100%;
}
.card-list .info .top {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      margin-bottom: 5px;
}
.card-list .info .detail {
      color: #3369E8;
      text-decoration: none;
      font-size: 15px;
}
.card-list .info .watch_link {
      display: block;
      font-size: 15px;
      color: #f25d8e;
      text-decoration: none;
}
.card-list .card-reveal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow-y: auto;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
.card-list .card-reveal::-webkit-scrollbar {
      width: 4px;
      background: none;
      border-radius: 4px;
}
.card-list .card-reveal::-webkit-scrollbar-thumb {
      border-radius: 4px;
      background: rgba(179, 179, 179, 0.5);
}
.card-list .card-reveal::-webkit-scrollbar-thumb:hover {
      background: rgba(179, 179, 179, 0.5);
}
.card-list .card-reveal::-webkit-scrollbar-thumb:active {
      background: rgba(179, 179, 179, 0.5);
}
.card-list .card-reveal.active {
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
.card-list .card-reveal p {
      font-size: 15px;
}
.card-list .close {
    position: absolute;
    top: 23px;
    right: 5px;
    width: 24px;
    height: 24px;
    background: url(data:image/png;base64,);
    background-size: 100%;
}
.card-list .reveal-title {
    margin: 0px;
    font-size: 20px;
    font-weight: 400;
    padding-right: 10px;
}
.card-list .reveal-mark {
    text-align: right;
    font-size: 15px;
}
.card-list .reveal-mark i {
      display: inline-block;
      vertical-align: middle;
      margin: -4px 0 0 10px;
      width: 20px;
      height: 20px;
      background: url(data:image/png;base64,);
      background-size: 100%;
}
